// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Invoice List Sidebar
import UiLoader from '@components/ui-loader'
// ** Columns

import Moment from "moment"
// import { columns } from './columns' 
// import AddQuiz from '../add'
// import { useHistory } from 'react-router-dom'

// import AddQuiz from '../add'
// ** Store & Actions
import { getcricketquiz, quiztogglestatus } from '../../../store/action'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'
// import Swal from 'sweetalert2' 
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, CustomInput, Input, Spinner, CardHeader } from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
// import withReactContent from 'sweetalert2-react-content's
import AppCollapse from '@components/app-collapse'


// ** Table Header
const CustomHeader = () => {

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">

    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.quiz)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [status, setStatus] = useState(false)
  const [dataPerPage, setDataPerPage] = useState('')
  // const [sidebarOpen, setSidebarOpen] = useState(false)
  // ** Get data on mount
  useEffect(() => {
    dispatch(getcricketquiz("", currentPage, dataPerPage))
  }, [currentPage, store.toggle_status])


  useEffect(() => {
    setCurrentPage(1)
    dispatch(getcricketquiz("", 1, dataPerPage))
  }, [dataPerPage])

  useEffect(() => {
    if (store.loadAllData) {
      <Spinner />
    } else {
      setListData(store.cric_data)
    }
  }, [store.loadAllData])

  //const columnsList = useCallback(columns(), [])
  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  // const handleConfirmText = (id) => {
  //   return MySwal.fire({
  //     title: 'Are you sure?',
  //     text: "You won't be able to revert this!",
  //     icon: 'warning',
  //     showCancelButton: true,
  //     confirmButtonText: 'Yes, delete it!',
  //     customClass: {
  //       confirmButton: 'btn btn-primary',
  //       cancelButton: 'btn btn-outline-danger ml-1'
  //     },
  //     buttonsStyling: false
  //   }).then(function (result) {

  //    if (result.value) {
  //       dispatch(deleteQuiz(id))
  //       MySwal.fire({
  //         icon: 'success',
  //         title: 'Deleted!',
  //         text: 'Your file has been deleted.',
  //         customClass: {
  //           confirmButton: 'btn btn-success'
  //         }
  //       })
  //     }
  //   })
  // }

  // const handleDelete = (id) => {
  //   dispatch(deleteQuiz(id))
  // }

  // const editHandler = (id) => {
  //   dispatch(getquizDetails(id))
  //   history.push(`/quiz/edit/${id}`)
  // }

  const SwitchClick = (row) => {

    dispatch(quiztogglestatus(row, status))

    setStatus(!status)
  }

  Moment().format("MMM Do YY")
  const columns = [
    {
      name: 'Sno',
      minWidth: '10px',
      selector: 's_no',
      cell: (row, pos) => store.cric_quiz_page.from + pos
    },
    {
      name: 'Name',
      minWidth: '250px',
      selector: 'quiz_name',
      sortable: true,
      cell: (row) => row.quiz_name
    },
    {
      name: 'Coins',
      minWidth: '200px',
      selector: 'quiz_coins',
      sortable: true,
      cell: (row) => row.quiz_coins
    },
    {
      name: 'Expiry',
      minWidth: '200px',
      selector: 'quiz_expiry',
      sortable: true,
      cell: (row) => Moment((row.quiz_expiry), 'DD-MM-YYYY').format("DD MMM YYYY HH:MM:SS")
    },
    {
      name: 'Status',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {

            (
              <CustomInput
                className='custom-control-success'
                type='switch'
                id={`success${row.quiz_id}`}
                name='success'
                onChange={() => SwitchClick(row.quiz_id)}
                inline
                defaultChecked={row.quiz_status !== "CLOSE"}
              />
            )
          }


        </div>
      )
    }
    // {
    //   name: 'Action',
    //   minWidth: '130px',
    //   selector: 'action',
    //   cell: (row) => (
    //     <div className="column-action">
    //       {/* <a href={`/quiz/edit/${row.quiz_id}`}>
    //         <Edit width="17" className="cursor-pointer" />
    //       </a> */}
    //        <Edit width="17" className="cursor-pointer" title="Edit Feed" onClick={() => editHandler(row.quiz_id)} />
    //       <Trash2
    //         width="17"
    //         className="ml-2 cursor-pointer"
    //         onClick={() => handleConfirmText(row.quiz_id)}
    //         />
    //     </div>
    //   )
    // }
  ]

  // ** Function in get data on rows per pag

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.cric_data.filter((userItem) => {
        const date = Moment((userItem.quiz_expiry), 'DD-MM-YYYY').format("DD MMM YYYY HH:MM:SS")
        const newserch =
          userItem.quiz_id.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
          userItem.quiz_name.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
          userItem.quiz_coins?.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) ||
          date.toLowerCase().startsWith(searchTerm.toLowerCase())

        const includes =
          userItem.quiz_id.toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
          userItem.quiz_name.toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
          userItem.quiz_coins?.toString().toLowerCase().includes(searchTerm.toLowerCase()) ||
          date.toLowerCase().includes(searchTerm.toLowerCase())

        if (newserch) {
          return newserch
        } else if (!newserch && includes) {
          return includes
        } else return null

      })

      setListData(filterList)
    } else {
      setListData(store.cric_data)
    }
  }, [searchTerm])

  if (store.loadAllData) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  console.log(store.cric_quiz_page.last_page)
  // ** Custom Pagination 
  const CustomPagination = () => {
    const count = store.cric_quiz_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allChallenges.length > 0) {
    //   return store.allChallenges
    // } else
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <Row xl={12}>
          <Col
            xl="12"
            className="d-flex align-items-sm-center justify-content justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1 ml-1">
              <Label className="mb-0" for="search-invoice">
                Search:
              </Label>
              <Input
                id="search-invoice"
                className="ml-50 w-100"
                type="text"
                value={searchTerm}
                onChange={(e) => handleFilter(e.target.value)}
              />
            </div>

          </Col>
        </Row>
      )
    }
  ]

  return (
    <Fragment>
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <CardHeader>
          <Col xl="12" className="d-flex align-items-center p-0">
            <div className="d-flex align-items-center w-100">
              <Label for="rows-per-page">Show</Label>
              <CustomInput
                className="form-control mx-50"
                type="select"
                id="rows-per-page"
                value={dataPerPage}
                onChange={(e) => setDataPerPage(e.target.value)}
                style={{
                  width: '5rem',
                  padding: '0 0.8rem',
                  backgroundPosition:
                    'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
                }}
              >
                <option value="10">10</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </CustomInput>
              <Label for="rows-per-page">Entries</Label>
            </div>
          </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
      {
        listData.length === 0 ? '' : <CustomPagination />
      }   
    </Fragment>
  )
}

export default UsersList
